<template>
	<view>
		<wybar @clickLeft="back" :titleText="title" :rightText="savetxt" @clickRight="save"></wybar>
		<view class="inputback">
			<input type="text" @input="input" :value="content" class="input">
		</view>
	</view>
</template>

<script>
	import wybar from '../../../components/wybar/wyBar.vue'
	export default {
		components: {
			wybar
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				userInfo: {},
				content: '',
				type:1,
				title:'',
				savetxt:''
			}
		},
		onLoad(e) {
			var that = this
			that.type = e.type
			if(that.type == 1){
				that.title = that.i18n.mine.shopname
				// uni.setNavigationBarTitle({
				// 	title:that.i18n.mine.shopname
				// })
				
			}else{
				that.title = that.i18n.mine.shopphone
				// uni.setNavigationBarTitle({
				// 	title:that.i18n.mine.shopphone
				// })
			}
			that.content = e.content
			that.savetxt = that.i18n.mine.save
			uni.getStorage({
				key: 'userinfo',
				success: res => {
					that.nickName = res.data.user_nickname
					console.log(res)
				}
			})
		},
		methods: {
			back() {
				uni.navigateBack({

				})
			},
			save(){
				if(this.type == 1){
					//name
					uni.setStorageSync('name_new',this.content)
				}else{
					uni.setStorageSync('phone_new',this.content)
				}
				this.back()
			},
			input(e) {
				this.content = e.target.value
				console.log(e.target.value)
			},
				
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #f5f6f7;
	}

	.inputback {
		margin-left: 15px;
		width: calc(100% - 30px);
		margin-top: 10px;
		height: 46px;
		border-radius: 8px;
		background-color: #FFF;
	}

	.input {
		margin-left: 15px;
		height: 46px;
		line-height: 46px;
		font-size: 16px;
		color: #333;
		width: calc(100% - 30px);
	}

	.save {
		left: 15px;
		width: calc(100% - 30px);
		height: 49px;
		border-radius: 8px;
		color: #FFF;
		text-align: center;
		font-size: 14px;
		font-weight: bold;
		background-color: #7900B2;
		position: absolute;
		bottom: 50px;
		line-height: 49px;
	}
</style>
